<template>
	<div>
		<el-dialog v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" label-width="$template2.back.add.form.base.labelWidth" :rules="rules">
				<el-row>
					<el-col :span="8">
						<el-form-item label="用户账号" prop="yonghuzhanghao">
							<el-input class="list_inp" v-model="form.yonghuzhanghao" placeholder="用户账号"
								 type="text" 								:readonly="!isAdd||disabledForm.yonghuzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="8">
						<el-form-item label="用户姓名" prop="yonghuxingming">
							<el-input class="list_inp" v-model="form.yonghuxingming" placeholder="用户姓名"
								 type="text" 								:readonly="!isAdd||disabledForm.yonghuxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="8">
						<el-form-item label="宠物名称" prop="chongwumingcheng">
							<el-input class="list_inp" v-model="form.chongwumingcheng" placeholder="宠物名称"
								 type="text" 								:readonly="!isAdd||disabledForm.chongwumingcheng?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="动物种类" prop="dongwuzhonglei">
							<el-input class="list_inp" v-model="form.dongwuzhonglei" placeholder="动物种类"
								 type="text" 								:readonly="!isAdd||disabledForm.dongwuzhonglei?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="动物病征" prop="dongwubingzheng">
							<el-input class="list_inp" v-model="form.dongwubingzheng" placeholder="动物病征"
								 type="text" 								:readonly="!isAdd||disabledForm.dongwubingzheng?true:false" />
              <el-button style="margin-left: 20px" @click="toReference()">
                <Search style="width: 1em; height: 1em; margin-right: 8px" />
                诊断参考
              </el-button>
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="是否打过狂犬疫苗" prop="shifoudaguokuangquanyimiao">
							<el-select
								class="list_sel"
								:disabled="!isAdd||disabledForm.shifoudaguokuangquanyimiao?true:false"
								v-model="form.shifoudaguokuangquanyimiao" 
								placeholder="请选择是否打过狂犬疫苗"
								>
								<el-option v-for="(item,index) in shifoudaguokuangquanyimiaoLists" :label="item"
									:value="item"
									>
								</el-option>
							</el-select>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="诊断结果" prop="zhenduanjieguo">
							<el-input class="list_inp" v-model="form.zhenduanjieguo" placeholder="诊断结果"
								 type="text" 								:readonly="!isAdd||disabledForm.zhenduanjieguo?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="8">
						<el-form-item label="医生账号" prop="yishengzhanghao">
							<el-input class="list_inp" v-model="form.yishengzhanghao" placeholder="医生账号"
								 type="text" 								:readonly="!isAdd||disabledForm.yishengzhanghao?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="8">
						<el-form-item label="医生姓名" prop="yishengxingming">
							<el-input class="list_inp" v-model="form.yishengxingming" placeholder="医生姓名"
								 type="text" 								:readonly="!isAdd||disabledForm.yishengxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item label="回访结果" prop="huifangjieguo">
							<el-input v-model="form.huifangjieguo" placeholder="回访结果" type="textarea"
							:readonly="!isAdd||disabledForm.huifangjieguo?true:false"
							/>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="closeClick">取消</el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"
						>
						提交
					</el-button>
				</span>
			</template>
		</el-dialog>
    <el-dialog
        title="诊断参考"
        v-model="refDialog.show"
        width="70%">
      <el-table :data="refDialog.refList" style="width: 100%">
        <el-table-column prop="dongwubingzheng" label="动物病症" />
        <el-table-column prop="zhenduanjieguo" label="诊断结果" />
        <el-table-column prop="huifangjieguo" label="回访结果" />
      </el-table>
      <div slot="footer" class="dialog-footer" style="text-align: center;margin-top: 20px">
        <el-button @click="refDialog.show = false">关 闭</el-button>
      </div>
    </el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//基础信息
	const tableName = 'binglidangan'
	const formName = '病历档案'
	//基础信息
	//form表单
	const form = ref({})
	const disabledForm = ref({
		yonghuzhanghao : false,
		yonghuxingming : false,
		chongwumingcheng : false,
		dongwuzhonglei : false,
		dongwubingzheng : false,
		shifoudaguokuangquanyimiao : false,
		zhenduanjieguo : false,
		huifangjieguo : false,
		yishengzhanghao : false,
		yishengxingming : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')

  const refDialog = ref({
    show:false,
    refList:[],
  })

  const toReference = async ()=>{
    let keyArr = form.value.dongwubingzheng.replace(/,|，/g,' ').split(/( )+/).filter(item=>!!item.trim())
    let list = []
    for(let i in keyArr){
      await context.$http.get('binglidangan/list',{
        params:{
          dongwubingzheng:`%${keyArr[i]}%`
        }
      }).then(res=>{
        list = list.concat(res.data.data.list)
      })
    }
    if(!list.length)return context.$toolUtil.message("暂无参考","warning")
    let nList = []
    list.forEach(item=>{
      if(!nList.find(item1=>(item1.id==item.id))){
        nList.push(item)
      }
    })
    refDialog.value.show = true
    refDialog.value.refList = nList
  }
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		yonghuzhanghao: [
		],
		yonghuxingming: [
		],
		chongwumingcheng: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		dongwuzhonglei: [
		],
		dongwubingzheng: [
		],
		shifoudaguokuangquanyimiao: [
		],
		zhenduanjieguo: [
		],
		huifangjieguo: [
		],
		yishengzhanghao: [
		],
		yishengxingming: [
		],
	})
	//表单验证
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//是否打过狂犬疫苗列表
	const shifoudaguokuangquanyimiaoLists = ref([])
	//methods

	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//重置
	const resetForm = () => {
		form.value = {
			yonghuzhanghao: '',
			yonghuxingming: '',
			chongwumingcheng: '',
			dongwuzhonglei: '',
			dongwubingzheng: '',
			shifoudaguokuangquanyimiao: '',
			zhenduanjieguo: '',
			huifangjieguo: '',
			yishengzhanghao: '',
			yishengxingming: '',
		}
	}
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '新增' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '查看' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '修改' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='yonghuzhanghao'){
					form.value.yonghuzhanghao = row[x];
					disabledForm.value.yonghuzhanghao = true;
					continue;
				}
				if(x=='yonghuxingming'){
					form.value.yonghuxingming = row[x];
					disabledForm.value.yonghuxingming = true;
					continue;
				}
				if(x=='chongwumingcheng'){
					form.value.chongwumingcheng = row[x];
					disabledForm.value.chongwumingcheng = true;
					continue;
				}
				if(x=='dongwuzhonglei'){
					form.value.dongwuzhonglei = row[x];
					disabledForm.value.dongwuzhonglei = true;
					continue;
				}
				if(x=='dongwubingzheng'){
					form.value.dongwubingzheng = row[x];
					disabledForm.value.dongwubingzheng = true;
					continue;
				}
				if(x=='shifoudaguokuangquanyimiao'){
					form.value.shifoudaguokuangquanyimiao = row[x];
					disabledForm.value.shifoudaguokuangquanyimiao = true;
					continue;
				}
				if(x=='zhenduanjieguo'){
					form.value.zhenduanjieguo = row[x];
					disabledForm.value.zhenduanjieguo = true;
					continue;
				}
				if(x=='huifangjieguo'){
					form.value.huifangjieguo = row[x];
					disabledForm.value.huifangjieguo = true;
					continue;
				}
				if(x=='yishengzhanghao'){
					form.value.yishengzhanghao = row[x];
					disabledForm.value.yishengzhanghao = true;
					continue;
				}
				if(x=='yishengxingming'){
					form.value.yishengxingming = row[x];
					disabledForm.value.yishengxingming = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('yishengzhanghao')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.yishengzhanghao = json.yishengzhanghao
				disabledForm.value.yishengzhanghao = true;
			}
			if(json.hasOwnProperty('yishengxingming')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.yishengxingming = json.yishengxingming
				disabledForm.value.yishengxingming = true;
			}
		})
		shifoudaguokuangquanyimiaoLists.value = "是,否".split(',')
	}
	//初始化
	//声明父级调用
	defineExpose({
		init
	})
	//关闭
	const closeClick = () => {
		formVisible.value = false
	}
	//富文本
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save=()=>{
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									formVisible.value = false
									emit('formModelChange')
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							formVisible.value = false
							emit('formModelChange')
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 表单
	.formModel_form{
		border: 1px solid rgba(254, 182, 203, 0.5);
		border-radius: 0px;
		padding: 40px 30px;
		margin: 0;
		background: #fff;
		// form item
		:deep(.el-form-item) {
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 color: #666;
			 background: none;
			 font-weight: 500;
			 display: block;
			 width: 150px;
			 text-align: right;
			 min-width: 150px;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
				width: calc(100% - 120px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 输入框
				.list_inp {
					border: 0px solid rgba(255, 255, 255, 0.25);
					border-radius: 0px;
					padding: 0 10px;
					color: #666;
					background: rgba(0, 0, 0, 0.1);
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					height: 36px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				// 下拉框
				.list_sel {
					border: 1px solid rgba(255, 255, 255, 0.25);
					border-radius: 0px;
					padding: 0 10px;
					color: #666;
					background: rgba(0, 0, 0, 0.1);
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					//去掉默认样式
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
							.el-input__wrapper{
								border: none;
								box-shadow: none;
								background: none;
								border-radius: 0;
								height: 100%;
								padding: 0;
							}
							.is-focus {
								box-shadow: none !important;
							}
						}
					}
				}
				// 长文本
				.el-textarea__inner {
					border: 1px solid rgba(255, 255, 255, 0.25);
					border-radius: 0;
					padding: 12px;
					color: #666;
					background: rgba(0, 0, 0, 0.1);
					width: auto;
					font-size: 14px;
					min-height: 120px;
					min-width: 350px;
				}
			}
		}
	}
	// 按钮盒子
	.formModel_btn_box {
		display: flex;
		width: 100%;
		justify-content: center;
		align-items: center;
		.formModel_cancel {
			border: 1px solid #d43f3a;
			cursor: pointer;
			border-radius: 3px;
			padding: 0 24px;
			margin: 0 20px 0 0;
			outline: none;
			color: #fff;
			background: rgba(217, 83, 79, 0.45);
			width: auto;
			font-size: 14px;
			min-width: 100px;
			height: 36px;
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
			border: 1px solid #357ebd;
			cursor: pointer;
			border-radius: 3px;
			padding: 0 24px;
			margin: 0 20px 0 0;
			outline: none;
			color: #fff;
			background: rgba(66, 139, 202, 0.55);
			width: auto;
			font-size: 14px;
			min-width: 100px;
			height: 36px;
		}
		.formModel_confirm:hover {
		}
	}
</style>